<template>
  <div id="nativeShare">
    <div class="label"></div>
    <div class="list">
      <span class="list-item" v-for="(button, index) in shareButtons" :key="index">
        <img :src="button.src" class="i" @click="call(button)">
        {{button.text}}
      </span>
    </div>
  </div>
</template>

<script>
import NativeShare from 'nativeshare'
import mShare from 'm-share'
export default {
  name: 'vue-share-native',
  data() {
    return {
      Buttons:[
        {text: '', nativeshare:'wechatFriend', m_share: 'wx' , src: require('../assets/weixin.png')},
        {text: '', nativeshare:'wechatTimeline', m_share: 'wxline', src: require('../assets/wxfriend.png')},
        {text: '', nativeshare:'weibo', m_share: 'sina', src: require('../assets/weibo.png')},
        {text: 'QQ', nativeshare:'qqFriend', m_share: 'qq', src: require('../assets/qq.png')},
        {text: 'QQ', nativeshare:'qZone', m_share: 'qzone', src: require('../assets/qqzone.png')},
        {text: '', nativeshare:'', m_share: '', src: require('../assets/more.png')},
      ]
    }
  },
  props: {
    config: Object,
    shareMenu: Array,
  },
  computed: {
    shareButtons() {
        if (!this.shareMenu) {
            return this.Buttons
        }
        return this.shareMenu.map(i => this.Buttons[i]).filter(i => i !== undefined)
    }
  },
  methods: {
    call(command) {
      let shareData = {  //nativeShare
          title: this.config.title,
          desc: this.config.desc,
          // link
          link: this.config.link,
          icon: this.config.url,
          // 
          success: this.config.success,
          fail: this.config.cancel,
      }
      let mShareData = {  //m-share
            title: this.config.title, // document.title
            desc: this.config.desc, // , head<meta name="description" content="desc" />
            link: this.config.link, // window.location.href
            imgUrl: this.config.img, // , img
            fnDoShare: this.config.success,
      }
      let nativeShare = new NativeShare()
      nativeShare.setShareData(shareData)
      try {
        nativeShare.call(command.nativeshare)
      } catch(e) {
        //iphoneqqapi
        //iphoneqqapi
        //iphoneqqnativeShare.call()nativeShare.call()m-share.to()
        mShare.to(command.m_share, mShareData)
      }
    }
  }
}
</script>

<style scoped>
#nativeShare {
  font-size: 13px;
}
  .label {
    font-size: 18px;
    text-align: center;
  }
  .list {
    text-align: center;
  }
    .list-item {
      width: 30%;
      display: inline-block;
      text-align: center;
      margin: 10px 0;
    }
      .i {
        width: 40px;
        height: 40px;
        display: block;
        margin: 0 auto;
        margin-bottom: 5px;
        background-size: cover;
      }
</style>


